.service-wrap {
    padding: 0 30px;
    &:nth-child(2n) {
        background-color: $request; } }
.service_item {
    margin: 0 auto;
    max-width: 1130px;
    padding: 30px 0;
    &.service {
        @include sm {
            flex-wrap: nowrap; }
        @include xs {
            flex-wrap: wrap; } }
    & .service__img {
        background-size: contain;
        width: 164px;
        height: 164px;
        padding-bottom: unset;
        background-color: $white;
        margin-right: 80px;
        flex-shrink: 0;
        @include sm {
            margin: 0 30px 30px 0; } }
    & .service__text {
        max-width: none;
        margin: 0;
        & p {
            margin: 35px 0 25px; } } }
