.service {
    display: flex;
    justify-content: space-between;
    @include sm {
        flex-wrap: wrap; }
    &__wrap {
        padding: 0 30px;
        max-width: 1190px;
        margin: 0 auto;
        @include ms {
            padding: 0 15px; } }
    &__text {
        max-width: 680px;
        margin-right: 40px;
        & p {
            line-height: 1.4; }
        @include sm {
            order: 2;
            margin-right: 0; } }
    &__title {
        color: $indigo-title;
        margin-bottom: 30px; }
    &__img {
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        width: 375px;
        max-width: 375px;
        height: 100px;
        padding-bottom: 33.2%;
        border-radius: 50%;
        overflow: hidden;
        @include sm {
            margin: 0 auto;
            padding: 0;
            height: 375px;
            margin-bottom: 30px; }
        @include ms {
            height: auto;
            padding-bottom: 100%;
            width: 100%;
            max-width: none; }
        &_order {
            order: -1; }
        &_square {
            border-radius: 0; }
        &_contain {
            background-size: contain; } }
    &__list {
        margin-top: 40px;
        margin-left: 25px; }
    &.service_promo {
        align-items: center;
        padding: 30px 0;
        @include sm {
            padding: 15px 0; }
        & .service__text {
            padding: 0 25px;
            margin: 0;
            @include sm {
                padding: 0; } }
        & .service__img {
            max-width: 401px;
            width: 401px;
            padding-bottom: 35.5%;
            @include sm {
                padding-bottom: 0;
                height: 401px; }
            @include ms {
                height: auto;
                padding-bottom: 100%;
                width: 100%;
                max-width: none; } } } }

.promo__video.video-wrap {
    padding: 20px 0;
    & .video {
        max-width: none;
        height: 477px;
        @include ms {
            height: 200px; } } }
