.news {
    &__container {
        max-width: 1190px;
        @include md {
            max-width: 970px; } }
    // &__button
    //     text-align: center
    //     padding: 40px 0
    //     & button
    //         min-width: 248px
    &__select {
        text-align: right;
        margin-bottom: 15px;
        @include sm {
            text-align: left; }
        & .select2-container--default .select2-selection--single .select2-selection__placeholder {
            color: $indigo;
            border-bottom: 1px dotted $indigo; }
        & .select2-container--default .select2-selection--single {
            border: none;
            background-color: transparent;
            border-radius: 0; }
        & .select2-container--default .select2-selection--single .select2-selection__rendered {
            color: $indigo;
            text-align: left;
            padding: 0; }
        & .select2-container--default .select2-selection--single .select2-selection__arrow b {
            border: none; }
        & .select2-container--default .select2-selection--single .select2-selection__arrow {
            height: 8px;
            width: 13px;
            background-image: url('../img/select-arr.svg');
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;
            position: absolute;
            top: 12px;
            right: 0px; }
        & .select2-dropdown.select2-dropdown--below {
            background-color: #fff;
            border: none;
            border-radius: 0;
            text-align: left; }
        & .select2-results__option {
            padding: 10px 18px; }
        & .select2-container--default .select2-results__option--highlighted[aria-selected] {
            color: $indigo;
            background-color: transparent;
            @include hover {
                color: $accent; } }
        & .select2-container--default .select2-results__option[aria-selected=true] {
            background-color: transparent; } } }

.grid {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
    @include xs {
        justify-content: center; }
    &__item {
        padding: 15px;
        @include xs {
            &:nth-child(9) {
                display: none; } } }
    &__item-25 {
        width: 25%;
        @include md {
            width: 33.3%; }
        @include sm {
            width: 50%; }
        @include xs {
            width: 100%;
            max-width: 280px; } }
    &__item-50 {
        width: 50%;
        @include md {
            width: 33.3%; }
        @include sm {
            width: 50%; }
        @include xs {
            width: 100%;
            max-width: 280px; } } }

.news-item {
    height: 100%;
    background-color: $white;
    display: flex;
    flex-wrap: wrap;
    @include xs {
        min-height: 430px; }
    &__container {
        width: 100%; }
    &__img {
        height: 245px;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover; }
    &__text {}
    & p {
        padding: 20px;
        @include f(16px, 500); }
    &__aside {
        width: 100%;
        text-align: right;
        padding: 0 20px 20px 20px;
        align-self: flex-end;
        & p {
            display: none; } }
    &__link {
        display: inline-block;
        transition: color .3s;
        @include hover {
            color: $accent; } }
    &.triple {
        // height: 30%
        height: 118px;
        background-color: transparent;
        &:not(:last-child) {
            margin-bottom: 29px; } } }
.triple {
    &__block {
        background-color: $white;
        display: flex;
        @include xs {
            flex-wrap: wrap; } }
    &__img {
        height: 118px;
        width: 198px;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        @include md {
            display: none; }
        @include xs {
            display: block;
            width: 100%;
            height: 245px; } }
    &__text {
        & p {
            @include md;
            padding-bottom: 0;
            @include xs {
                width: 100%; } }
        @include xs {
            display: flex;
            flex-wrap: wrap; } }
    &__aside {
        text-align: right;
        padding-top: 10px;
        padding-right: 10px;
        @include xs {
            align-self: flex-end;
            padding: 10px;
            margin-left: auto; } }
    &__link {
        transition: color .3s;
        @include hover {
            color: $accent; } } }



.news-item_full {
    display: block;
    .news-item__container {
        display: none; }
    .news-item__aside {
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        box-shadow: inset 0px -115px 200px 0px $white;
        & p {
            display: block;
            width: 100%;
            padding: 0;
            padding-bottom: 20px;
            text-align: left; } } }
