.introduction {
    margin-top: -50px;
    &__container {
        padding: 0 60px;
        max-width: 1250px; }
    &__inner {
        margin-left: -50px;
        @include clr; }
    &__left {
        float: left;
        padding: 0 50px 30px 0;
        width: 50%;
        max-width: 559px; }
    &__right {} }

.task {
    padding: 34px 42px 42px 42px;
    background-color: $request;
    &__item {
        &:not(:first-child) {
            margin-top: 35px; }
        & p {
            line-height: 1.5;
            max-width: 400px; }
        & .list__item:not(:last-child) {
            margin-bottom: 15px; } }
    &__title {
        margin-bottom: 30px;
        color: $indigo-title; }
    &__list {
        margin-left: 30px; } }

.partner {
    &__logo-wrap {
        width: 160px;
        height: 160px;
        background-color: $request;
        border-radius: 50%;
        margin-bottom: 35px;
        position: relative;
        display: inline-block;
        @include pseudo;
        &:before, &:after {
            display: block;
            width: 222px;
            height: 222px;
            border-radius: 50%;
            border: 1px solid rgba(225, 225, 225, 0.1);
            @include vcenter; }
        &:after {
            width: 467px;
            height: 467px; } }
    &__logo {
        max-width: 100%;
        @include vcenter; }
    &__title {
        color: $indigo-title; }
    & p {
        margin-top: 30px;
        line-height: 1.5; }
    &__button {
        margin-top: 35px; } }
.icon-arr-left_sm {
    font-size: 14px;
    fill: $accent;
    position: absolute;
    top: {}
    left: 0;
    transition: fill .3s; }
