.top-full {
	color: $indigo-title;
	&-img {
		display: none;
		@include sm {
			display: block;
			width: 100%; } }
	&__bg {
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		height: 454px;
		box-shadow: inset 0 140px 475px 0px $white;
		@include sm {
			background-image: none!important;
			height: auto;
			box-shadow: none; } }
	&__container {
		height: 100%;
		display: flex;
		align-items: center; }
	&__wrap {
		margin: -115px 0 0 75px;
		@include sm {
			margin: 0;
			padding-bottom: 50px; } }
	&__title {
		padding: 20px 0; } }
