  height: 411px;
  position: relative;
  margin: 0 auto;
}

@media only screen and (max-width: 784px) {
  .video {
    height: 0;
    padding-bottom: 57%;
  }
}

.video__item {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.products {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

@media only screen and (max-width: 784px) {
  .products {
    justify-content: center;
  }
}

.products__item {
  width: 50%;
  margin-bottom: 50px;
}

@media only screen and (max-width: 784px) {
  .products__item {
    width: 100%;
  }
}

.products__item:nth-child(2n) .products__link {
  margin-left: auto;
}

.products__link {
  display: block;
  width: 100%;
  max-width: 477px;
  border-radius: 50%;
  position: relative;
}

.no-touch .products__link:hover .products__title {
  color: #fff;
  z-index: 2;
}

.no-touch .products__link:hover:after {
  animation: scale-up-center 0.3s linear both;
}

.no-touch .products__link:hover:before {
  animation: border 0.1s 0.3s linear forwards;
}

.products__link:after {
  background-color: rgba(65, 91, 113, 0.8);
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  transform: scale(0);
}

.products__link:before {
  display: block;
  width: 99%;
  height: 99%;
  border-radius: 50%;
  border: 2px solid #4e6e8a;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media only screen and (max-width: 784px) {
  .products__link {
    margin: 0 auto;
  }
}

.products__inner {
  overflow: hidden;
  padding-bottom: 100%;
  height: auto;
  border-radius: 50%;
  background-color: #fff;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: bottom;
  position: relative;
}

.products__title {
  position: absolute;
  top: 23%;
  left: 50%;
  transform: translateX(-50%);
  width: 80%;
  text-align: center;
  color: #405b71;
  transition: color .5s;
}

@keyframes scale-up-center {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes border {
  0% {
    width: 99%;
    height: 99%;
  }
  100% {
    width: 105%;
    height: 105%;
  }
}

